.Modal{
  font-family: Futura, "Trebuchet MS", Arial, sans-serif;
  display: none;
  &__Overlay{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0,0,0,0.8);
    z-index: 0;
  }
  &__Button{
    position: fixed;
    right: 5rem;
    top: 2rem;
    appearance: none;
    border: none;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
    user-select: none;
    width: 24px;
    background: transparent;
    outline: none;
    z-index: 3;
  }
  &__Close{
    &::before,
    &::after{
      position: absolute;
      content: "";
      display: block;
      width: 24px;
      height: 2px;
      background: #fff;
      border-radius: 2px;
    }
    &::before{
      transform: rotate(45deg);
    }
    &::after{
      transform: rotate(-45deg);
    }
  }
  &__Block{
    width: 800px;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
  }
  &__Names{
    width: 500px;
  }
  &__Members{
    display: flex;
  }
  &__Member{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
  &__MemberImage{
    width: 80px;
    height: 80px;
    img{
      border-radius: 50%;
    }
  }
  &__Text{
    margin-top: 30px;
    font-size: 1.4rem;
    font-weight: bold;
    line-height: 1.7em;
  }
}
