.Hero {
  overflow: hidden;
  background-attachment: fixed;
	width: 100%;
	height: 660px;
	margin-top: -60px;
	display: flex;
	justify-content: center;
	align-items: center;
  overflow: hidden;
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
  iframe{
    position: fixed;
    z-index: -1;
    width: 100%;
    height: 120vh;
  }
  &__Logo{
    width: 80px;
    height: 80px;
    opacity: 0;
  }
}

.Sticker {
  position: relative;
  background-color: #fff;
	&__Block {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-items: center;
  }

	&__Item {
		width: 70%;
		height: 410px;
		background-color: #eee;
    img{
      height: 410px;
      object-fit: cover;
      width: 100%;
    }
	}
}
.flex-between{
  justify-content: space-between;
}
.flex-end{
  justify-content: flex-end;
}
.align-end{
  align-self: flex-end;
}
.align-center{
  align-self: center;
}
